body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-container {
  .login-container {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
  }
  .title {
    text-align: center;
    font-size: 23px;
    font-weight: 700;
    padding-top: 30px;
  }
  .text {
    font-weight: 700;
    padding-bottom: 3px;
  }
  input {
    padding: 8px 5px;
    outline: none;
    background: #1618230f;
    border: #1618230f;
    margin-bottom: 5px;
    width: 100%;

    &:hover {
      background-color: rgb(213, 224, 219);
      // border: 1px solid #dedede;
    }

  }
  button {
    margin-top: 15px;
    padding: 10px;
    border-radius: 3px;
    //background: deeppink ;
    outline: none;
    border: none;
  

    &.active {
      background-color: #fe2c55;
      color: white;
    }
  }
  .back {
    text-align: center;
    margin-top: 20px;
    cursor: pointer;
  }
  .input-2{
    position: relative;
    i {
      position: absolute;
      top: 17px;
      right: 10px;
      cursor: pointer;
    }
    
  }
  button[disabled=disabled], button:disabled {
    cursor: not-allowed;
    // pointer-events: none;
    }
}